* {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
}

body {
}

header {
  height: 60px;
  display: flex;
  align-items: center;
  background-color: rgb(255, 55, 55);
}

header > img {
  height: 26px;
  padding: 18px;
}

header > h1 {
  font-size: 22px;
  font-weight: 600;
  letter-spacing: -1px;
  color: rgb(241, 241, 241);
}

.formula {
  margin: 20px auto auto;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  font-weight: bolder;
  color: rgb(255, 55, 55);
}

.inputs {
  padding: 24px;
  display: flex;
  flex-direction: column;
  grid-gap: 6px;
  gap: 6px;
}

.input-container {
  display: flex;
  padding: 4px 0;
  border-bottom: 1px solid rgb(202, 202, 202);
}

label {
  font-size: 20px;
}

input {
  padding-right: 6px;
  width: 100%;
  flex: 1 1;
  border: none;
  text-align: end;
  font-size: 16px;
}

input:focus {
  outline: none;
}

button {
  margin-left: 24px;
  margin-bottom: 24px;
  padding: 6px 12px;
  background-color: rgb(255, 55, 55);;
  border: 1px solid rgb(255, 55, 55);
  color: white;
}

.steps {
  display: flex;
  flex-direction: column;
  box-shadow: rgba(149, 157, 165, 0.2) 0px 4px 12px;

}

.step {
  display: flex;
  flex-direction: column;
  border-bottom: 1px solid rgb(241, 241, 241);
}

.step:last-of-type {
  border: none;
}

.step > * {
  padding: 12px 24px;
}

.step-main {
  display: flex;
  align-items: center;
}

.step-value {
  border-top: 1px solid rgb(231, 231, 231);
  color: gray;
}

.sub-steps {
  display: flex;
  flex-direction: column;
  grid-gap: 12px;
  gap: 12px;
  border-top: 1px solid rgb(231, 231, 231);
  color: gray;
}


